.block-facets{
  padding: 30px;
  background-color: $brand-primary-o;
  .block-title{
    position: relative;
    margin-bottom: 20px;
    cursor: pointer;
    &:before{
      position: absolute;
      right: 0;
      color: $brand-primary;
      font-size: $facet-block-toggle-size;
      line-height: $font-size-h3;
      transition: transform .1s linear;

      @extend .glyph;
      @extend .glyph-close;
    }
    &[aria-expanded="false"]{
      &:before{
        transform: rotate(45deg);
      }
    }
  }
  label{
    font-size: $facet-block-text-size;
  }
  [type="checkbox"]:checked + label{
    font-weight: bold;
  }
}

.facets-widget-checkbox{
  .item-list__checkbox{
    margin: 0;
    padding: 0;
    .facet-item{
      margin-bottom: 5px;
      list-style: none;
      .facets-widget-{
        padding: 10px 0;
      }
    }
  }
}